*{
    box-sizing: border-box;
    margin: 0;
    font-family: 'Albert Sans', sans-serif;
    --main-text-color:rgb(0,14,50);
}
svg{
    position: absolute;
}
.content{
    width: 100vw;
    min-height: 100vh;
    background-color: green;
    display: flex;
}
.contentWrapper{
    width: 100vw;
    background-color: olive;
    display: flex;
    justify-content: end;
}
.largeSection{
    width: 60%;
    height: 100%;
}
.smallSection{
    width: 30%;
    height: 100%;
    background-color: orange;
    display: flex;
    flex-flow: column;
    align-items: center;
    justify-content: space-evenly;
}
.photo{
    width: 200px;
    height: 200px;

    border-radius: 50%;
    background-color: red;
}
.app{
    /*position: absolute;*/
    /*top: 0;*/
    /*left: 0;*/
    /*bottom: 0;*/
    /*right: 0;*/
    /*display: flex;*/
    /*flex-flow: column;*/
}
.canvas{
    position: absolute;
    top: 0;
    filter: url('#goo');
}
.sitesList{
    font-size: 20px;
    color: rgb(168 190 208);
    mix-blend-mode: luminosity;
}
.projectsSection{
    background-color: green;
}

.sectionTitle{
    font-size: 30px;
color:var(--main-text-color)
}
.aside{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    width: 50px;
}
.name{
    color: white;
    position: absolute;
    bottom: 32%;
    width: 200px;
    left: -80%;
    transform: rotate(-90deg);
    letter-spacing: 3px;
}
.name::after{
    content: '';
    position: absolute;
    height: 3px;
    bottom: -10px;
    left: 0;
    width: 50%;
    background: white;
    transition: all 500ms ease;
}
.name:hover::after{
    width: 80%;
}